
import { CodeIcon } from "@heroicons/react/solid";
import React from "react";
import kti from '../assets/img/kti.png'
import layarapp from '../assets/img/layarapp.png'
import mythpedia from '../assets/img/mythpedia.png'
import liligaan from '../assets/img/liligaan.png'

const projects = [
    {
      title: "PT. Konstruksi Telekomunikasi Indonesia",
      subtitle: "Company Profile and Internal Website",
      description:
        "Manage employees, manage and monitor ongoing project processes, manage loan and reimbursement approvals. Built using CodeIgniter 3, Bootstrap, Datatable, Etc.",
      image: kti,
      link: "https://ktindo.com/",
    },
    {
      title: "Submission at Dicoding Course",
      subtitle: "Progressive Web Apps",
      description:
        "Liligaan is information website about Primera Division League, this website is Submission at Dicoding Progressive Web Apps Course,  Data source from Football-data API.",
      image: liligaan,
      link: "https://liligaan.netlify.app/",
    },
    {
      title: "ReactJS Bootcamp at Sanbercode",
      subtitle: "Movies and Game Website",
      description:
        "Manage movie and game, build using ReactJS implementing Hooks, State Management Context, And Authentication, Data source from Sanbercode API.",
      image: layarapp,
      link: "https://layarapp.netlify.com/",
    },
    {
      title: "Submission at Dicoding Course",
      subtitle: "Progressive Web Apps",
      description:
        "Mythpedia is website about several popular myth in Indonesia, this website is Submission at Dicoding Progressive Web Apps Course. ",
      image: mythpedia,
      link: "https://mythpedia.netlify.com/",
    },
  ];

const Projects = () => {
    return(
        <section id="projects" className="text-gray-400 bg-gray-900 body-font">
          <div className="container px-5 py-10 mx-auto text-center lg:px-40">
            <div className="flex flex-col w-full mb-20">
              <CodeIcon className="mx-auto inline-block w-10 mb-4" />
              <h1 className="sm:text-4xl text-3xl font-medium title-font mb-4 text-white">
                Apps I've Built
              </h1>
              <p className="lg:w-2/3 mx-auto leading-relaxed text-base">
                Here are some of the projects that I have worked on, professional projects and assignments projects.
              </p>
            </div>
            <div className="flex flex-wrap -m-4">
              {projects.map((project) => (
                <a
                  href={project.link}
                  key={project.image}
                  className="sm:w-1/2 w-100 p-4">
                  <div className="flex relative">
                    <img
                      alt="gallery"
                      className="absolute inset-0 w-full h-full object-cover object-center"
                      src={project.image}
                    />
                    <div className="px-8 py-10 relative z-10 w-full border-4 border-gray-800 bg-gray-900 opacity-0 hover:opacity-100">
                      <h2 className="tracking-widest text-sm title-font font-medium text-green-400 mb-1">
                        {project.subtitle}
                      </h2>
                      <h1 className="title-font text-lg font-medium text-white mb-3">
                        {project.title}
                      </h1>
                      <p className="leading-relaxed">{project.description}</p>
                    </div>
                  </div>
                </a>
              ))}
            </div>
          </div>
        </section>
    )
}

export default Projects